.hero-section {
  background-color: $background-products-rose;
}

.container-hero {
  z-index: 1;
  position: relative;
  overflow: hidden;
  @media screen and(min-width: $tablet) {
    display: flex;
    justify-content: space-between;
  }
}

.hero-section {
  background-color: $background-products-rose;
}

.hero-left-part {
  @media screen and (min-width: $tablet) {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-top: 100px;
    margin-bottom: 28px;
  }
  @media screen and (min-width: $desktop) {
    margin-top: 170px;
    margin-bottom: 44px;
  }
}

.hero-title {
  z-index: 5;
  margin: 140px 0 210px;
  width: 220px;
  @media screen and(min-width: $tablet) {
    width: 165px;
    margin: 15px 0 50px;
  }
  @media screen and(min-width: $desktop) {
    width: 280px;
    margin: 15px 0 110px;
  }
  &__mini {
    margin: 0;
    font-family: Titan One;
    font-weight: normal;
    font-size: 26px;
    line-height: 30px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $text-color-white;
    margin-bottom: 20px;
    @media screen and(min-width: $tablet) {
      font-size: 22px;
      line-height: 25px;
    }
    @media screen and(min-width: $desktop) {
      font-size: 38px;
      line-height: 44px;
      margin-bottom: 25px;
    }
  }
  &__larger {
    font-family: Titan One;
    font-weight: normal;
    font-size: 48px;
    line-height: 96.5%;
    text-transform: uppercase;
    color: $text-color-chery;
    @media screen and(min-width: $tablet) {
      font-size: 34px;
      line-height: 90.5%;
    }
    @media screen and(min-width: $desktop) {
      font-size: 58px;
      line-height: 90.5%;
    }
  }
}

.hero-btn {
  display: flex;
  &__first-item {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 8px;
    @media screen and(min-width: $desktop) {
      margin-right: 15px;
    }
  }
  &__second-item {
    display: flex;
    align-items: center;
    justify-content: center;
    @media screen and(min-width: $desktop) {
    }
  }
  &__products {
    padding: 7px 14px;
    color: $text-color-white;
    border-radius: 20px;
    border: transparent;
    background-color: $text-color-chery;
    font-family: DM Sans;
    font-weight: normal;
    font-size: 8px;
    line-height: 10px;
    @include transit(background-color);
    @media screen and(min-width: $desktop) {
      font-size: 14px;
      line-height: 18px;
      padding: 10px 20px;
    }
    &:hover,
    &:focus {
      background: $background-hero-hover;
      box-shadow: 0 0 5px 0 $text-color-rose inset, 0 0 2px 5px $text-color-rose;
    }
  }
  &__howMade {
    padding: 7px 14px;
    border-radius: 20px;
    border: transparent;
    color: $text-color-chery;
    background-color: $text-color-white;
    font-family: DM Sans;
    font-weight: normal;
    font-size: 8px;
    line-height: 10px;
    text-align: center;
    @include transit(background-color);
    @media screen and(min-width: $desktop) {
      font-size: 14px;
      line-height: 18px;
      padding: 10px 20px;
    }
    &:hover,
    &:focus {
      background-color: $background-hero-hover2;
      box-shadow: 0 0 5px 0 $text-color-rose inset, 0 0 2px 5px $text-color-rose;
    }
  }
}

.hero-info-left-box {
  position: relative;
  @media screen and (min-width: $tablet) {
    margin-left: 120px;
  }
  @media screen and (min-width: $desktop) {
    margin-left: 180px;
  }
}

.hero-text-about-milk {
  font-family: DM Sans;
  font-weight: normal;
  font-size: 8px;
  line-height: 150%;
  color: $text-color-white;
  @media screen and (min-width: $tablet) {
    width: 115px;
  }
  @media screen and (min-width: $desktop) {
    width: 200px;
    font-size: 14px;
    line-height: 140%;
  }
}

.hero-want-mo-text {
  display: flex;
  margin-top: 8px;
  @media screen and (min-width: $desktop) {
    margin-top: 20px;
  }
  &__link {
    padding: 4px 8px;
    border-radius: 50%;
    background-color: $text-color-white;
    // &:hover,
    // &:focus {
    //     animation-name: arrowRotate;
    //     animation-duration: 1000ms;
    //     animation-timing-function: linear;
    //     animation-fill-mode: forwards;
    // }
  }
  &__arrow {
    fill: $text-color-chery;
  }
}

// animation arrow
// @keyframes arrowRotate {
//     0% {
//         transform: rotate(0deg);
//     }
//     100% {
//         transform: rotate(90deg);
//     }
// }
.hero-info-left-box {
  display: none;
  @media screen and(min-width: $tablet) {
    display: flex;
    align-items: flex-end;
  }
}

.hero-pics-left {
  display: none;
  animation: milk-pulse 0.9s infinite ease-out;
  @media screen and(min-width: $tablet) {
    display: block;
    position: absolute;
    bottom: 20px;
    left: 34px;
  }
  @media screen and(min-width: $desktop) {
    bottom: 30px;
    left: 75px;
  }
}

@keyframes milk-pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

// .ice-cream-box
.hero-png-box {
  &__img {
    position: absolute;
    left: 47%;
    bottom: 0;
    z-index: -2;
    animation-name: slideInUpIce;
    animation-duration: 1s;
    animation-fill-mode: both;
  }
  &__circle {
    top: 50%;
    left: 50%;
    transform: translate(-35%, -55%);
    position: absolute;
    z-index: -3;
    background-color: $background-hero-rose;
    width: 345px;
    height: 345px;
    border-radius: 50%;
    @media screen and(min-width: $tablet) {
      width: 325px;
      height: 325px;
      left: 54%;
    }
    @media screen and(min-width: $desktop) {
      width: 540px;
      height: 540px;
      left: 50%;
    }
  }
}

@keyframes slideInUpIce {
  0% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
    visibility: visible;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

// правый вверх
.hero-info-right-box {
  display: none;
  @media screen and(min-width: $tablet) {
    display: block;
    margin-top: 95px;
  }
  @media screen and (min-width: $desktop) {
    margin-top: 160px;
  }
}

.amount-of-points {
  position: relative;
  @media screen and(min-width: $tablet) {
    font-family: Titan One;
    font-weight: normal;
    font-size: 28px;
    line-height: 32px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $text-color-chery;
  }
  @media screen and (min-width: $desktop) {
    font-size: 46px;
    line-height: 53px;
  }
  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    right: 0;
    border-radius: 50%;
    width: 16px;
    height: 16px;
    background-color: $text-color-white;
    @media screen and (min-width: $desktop) {
      width: 30px;
      height: 30px;
    }
  }
  &__text {
    color: $text-color-white;
    font-family: DM Sans;
    font-weight: normal;
    font-size: 8px;
    line-height: 10px;
    text-align: center;
    @media screen and(min-width: $desktop) {
      font-size: 14px;
      line-height: 18px;
    }
  }
}

.hero-pics-right {
  display: none;
  animation-name: slideInUpGirl;
  animation-duration: 1100ms;
  animation-fill-mode: both;
  animation-delay: 600ms;
  @media screen and(min-width: $tablet) {
    display: block;
    position: absolute;
    bottom: 0;
    right: 34px;
  }
  @media screen and(min-width: $desktop) {
    right: 100px;
  }
}

@keyframes slideInUpGirl {
  0% {
    transform: perspective(500px) rotateY(90deg);
  }
  100% {
    transform: perspective(500px) rotateY(0deg);
  }
}

// кнопка вверх

.back_to_top {
    display: none;
    position: fixed;
    bottom: 150px;
    right: 3%;
    padding: 15px 20px;
    border-radius: 50%;
    z-index: 20;
    background-color: $background-hero-hover2;
    @include transit (background-color);
    opacity: 0.6;
    &:hover,
    &:focus,
    &:hover .back_to_top__svg,
    &:focus .back_to_top__svg {
        background-color: $background-products-rose;
        box-shadow: 0 0 1px 0 $text-color-rose inset, 0 0 1px 1px $text-color-rose;
        fill: $text-color-white;
        opacity: 1;
    }
    &__svg {
        fill: $text-color-rose;
        @include transit (fill);
    }
    &-show {
        display: block;
    }
}

